import React, { useState } from "react";
import {
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Button,
  Typography,
  Avatar,
} from "@material-ui/core";
import styled from "@emotion/styled";
import { TASK_G } from "utils/colors";
import { css } from "@emotion/core";
import { useSelector } from "react-redux";
import {
  selectAllMembers,
  selectMembersEntities,
} from "features/member/MemberSlice";
import { ITask, SpectatorMember } from "types";

interface Props {
  task: ITask;
}

const List = styled.div`
  display: flex;
  align-items: center;
  margin: 0.5rem 0;
  overflow-wrap: anywhere;
`;

const SpectatorDialog = ({ task }: Props) => {
  const membersById = useSelector(selectMembersEntities);
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [pendingAssignees, setPendingAssignees] = useState<SpectatorMember[]>(
    []
  );
  console.log(pendingAssignees);
  const members = useSelector(selectAllMembers);
  const assignees = task.spectators.map(
    (id) => membersById[id]
  ) as SpectatorMember[];
  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Typography
        variant="body1"
        onClick={handleClickOpen}
        style={{ cursor: "pointer" }}
      >
        <Button
          onClick={handleClickOpen}
          data-testid="delete-task"
          size="small"
          css={css`
            font-size: 12px;
            font-weight: bold;
            color: ${TASK_G};
            margin-bottom: 2rem;
          `}
        >
          Spectators
        </Button>
      </Typography>
      <Dialog
        open={open}
        onClose={handleClose}
        fullWidth
        maxWidth="sm" // Use maxWidth="lg" for large dialog
      >
        <DialogTitle>Spectators</DialogTitle>
        <DialogContent>
          {assignees.map((assignee) => (
            <List key={assignee.id}>
              <div>
                <Avatar
                  css={css`
                    height: 2rem;
                    width: 2rem;
                    margin-right: 0.5rem;
                  `}
                  src={assignee.avatar?.photo}
                  alt={assignee.avatar?.name}
                >
                  {assignee.username.charAt(0)}
                </Avatar>
              </div>
              <div>{assignee.username}</div>
            </List>
          ))}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

export default SpectatorDialog;
